<template>
  <div>
    <van-nav-bar title="影院" @click-left="handleLeft" @click-right="handleRight" ref="navbar">
      <template #left>
        {{ cityName }}
        <van-icon name="arrow-down" color="black" />
      </template>
      <template #right>
        <van-icon name="search" size="25" color="black" />
      </template>
    </van-nav-bar>

    <!-- 单页面的组件一般放在views文件夹中,可复用的组件一般放在components文件夹中 -->
    <!-- <h1>cinema</h1> -->
    <!-- 滚动优化 使用betterScroll 实现长列表滚动的性能优化 -->
    <div class="box" :style="{
      height: height
    }">
      <cinemas-list :cinemaslist="cinemaList"></cinemas-list>
    </div>
  </div>
</template>

<script>
import cinemasList from '@/components/cinemas/CinemasList.vue'
import BetterScroll from 'better-scroll'
// vue的新写法 mapState映射一个状态出来
import { mapState, mapActions, mapMutations } from 'vuex'

export default {
  data() {
    return {
      cinemasList: [],
      height: ''
    }
  },
  /* computed: {
    // vuex的新写法
    ...mapState(['cinemaList']) // 函数调用,返回的是一个对象,将其展开
  },
  // 因为返回的是对象,所以可以写成
  computed: mapState(['cinemaList']),

  // 新写法的原理就是
  computed: {
    cinemaList() {
      return this.$store.state.cinemaList
    } 
  },

  // 因此,最终使用的时候,直接写this.cinemaList就等价于this.$store.state.cinemaList
  */
  computed: mapState(['cinemaList', 'cityId', 'cityName']),
  mounted() {
    // 动态计算滚动的高度
    this.height = document.documentElement.clientHeight - document.querySelector('footer').offsetHeight - this.$refs.navbar.$el.offsetHeight + 'px'
    // console.log(this.$refs.navbar);

    // dispatch分发的意思，进到cinamas页面立即分发getCinemasData函数，并传来cityId
    // this.$store.dispatch('getCinemasData', this.$store.state.cityId) //可以将参数传给actions
    /*步骤 
    1.通过this.$store.dispatch分发actions函数,并传参数.
    2.在vuex中,在actions中定义该函数,接收参数,发起异步请求
    3.发起异步请求后获得的数据,通过store.commit传给mutations进行缓存
    4.在mutations中定义函数接收数据
    */

    // if判断,解决切换页面时重复请求的问题
    if (this.cinemaList.length === 0) {
      this.$store.dispatch('getCinemasData', this.cityId).then(() => {
        // this.$store.dispatch是异步请求,返回的是promise对象,可以使用.then方法
        // console.log('数据完事了');
        this.$nextTick(() => {
          new BetterScroll('.box', {
            scrollbar: {
              // 设置滚动条
              fade: true
            }
          })
        })
      })
    } else {
      // console.log('走缓存');
      //   // 使用better-scroll初始化,但是初始化会过早,应该使用nextTick
      this.$nextTick(() => {
        new BetterScroll('.box', {
          scrollbar: {
            // 设置滚动条
            fade: true
          }
        })
      })
    }

    // http({
    //   url: `/gateway?cityId=${this.$store.state.cityId}&ticketFlag=1&k=5295042`,
    //   headers: {
    //     'X-Host': 'mall.film-ticket.cinema.list'
    //   }
    // }).then(res => {
    //   // console.log(res.data.data);
    //   this.cinemasList = res.data.data.cinemas

    //   // 使用better-scroll初始化,但是初始化会过早,应该使用nextTick
    //   this.$nextTick(() => {
    //     new BetterScroll('.box', {
    //       scrollbar: {
    //         // 设置滚动条
    //         fade: true
    //       }
    //     })
    //   })
    // })
  },
  methods: {
    // mapActions mapMutations 需要映射在methods中
    ...mapActions(['getCinemasData']), // 把store中的actions方法,映射为本地方法,使用的时候直接this.getCinemasData
    ...mapMutations(['clearCinemaList']), // 把store中的mutations方法,映射为本地方法,使用的时候直接this.clearCinemaList

    handleLeft() {
      this.$router.push('/city')

      // 清空cinemaList
      // this.$store.commit('clearCinemaList')
      this.clearCinemaList() //使用vuex新写法就可以直接调用
    },
    handleRight() {
      this.$router.push('/cinemas/search')
    }
  },
  components: {
    cinemasList
  }
}
</script>

<style lang="scss" scoped>
// .box 是为了better-scroll的性能优化
.box {
  // height: 500px;
  overflow: hidden;
  // 修正滚动条位置
  position: relative;
}
</style>